.footer {
   &__container {
      display: flex;
      align-items: center;
      max-width: 1240px;
      @include xs {
         flex-direction: column;
         align-items: center; } }
   &__top {
      padding: 26px 0 22px;
      @include xs {
         padding: 25px 0 15px; } }
   &__bottom {
      padding: 33px 0 47px;
      background-color: #F9F9F9;
      @include sm {
         padding: 25px 0; } }
   &__logo {
      margin-right: 166px;
      @include md {
         margin-right: 65px; }
      @include sm {
         margin-right: 35px; }
      @include xs {
         display: none; } }
   &__contact-block {
      position: relative;
      top: -5px;
      flex: 0 0 184px;
      width: 184px;
      margin-left: auto;
      margin-right: 50px;
      @include sm {
         margin-right: 0; }
      @include xs {
         flex: 0 1 auto;
         margin: 0; } }
   &__copyright {
      flex: 0 0 208px;
      width: 208px;
      margin-left: 4px;
      margin-right: 60px;
      @include f('man400', 10, 13, 0);
      @include sm {
         margin-right: auto; }
      @include xs {
         flex: 0 1 auto;
         margin: 0 0 15px;
         text-align: center; }
      a {
         color: #D5B893; } }
   &__dev {
      margin-left: auto;
      margin-right: 50px;
      color: #21262D;
      @include f('man500', 11, 13, 0);
      @include sm {
         margin-right: 0; }
      @include xs {
         margin-right: auto; } }
   &__scroll-top-btn {
      position: relative;
      top: 5px;
      @include xs {
         top: auto;
         margin-bottom: 15px; } } }
